body {
  margin: 0;
  padding: 0;
  /* background:rgb(23, 9, 53); */
  background: rgb(48, 73, 213);
  background: radial-gradient(circle, rgba(48, 73, 213, 0.9962359943977591) 5%, rgba(6, 8, 19, 1) 100%);
  color: #1E1E1F;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}

p {
  color: aqua;
}
.TodoCounter {
    font-size: 24px;
    text-align: center;
    background: rgb(48, 158, 213);
    background: radial-gradient(circle, rgba(48, 158, 213, 1) 70%, rgba(15, 22, 61, 1) 100%);
    border: none;
}

.container-TodoSearch {

  width: calc(100% - 62px);
  margin: 50px;
  position: relative;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;

}

.TodoSearch {
  width: calc(100% - 50px);
  height: 30px;
  margin: 30px auto;
  padding:0px 10px;
  position: absolute;

  border-radius: 50px;
  border: none;
  box-shadow: 0px 5px 50px rgba(238, 236, 238, 0.25);/*Acá definimos el border-shadow*/
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;

  
}

.TodoSearch__button {
  height: 30px;
  position: absolute;
  right: 15px;
  border-radius: 50px;

  border: none;
  outline: none;
  background-color: transparent;
}

.TodoSearch:hover {
  background-color:rgba(131, 224, 145, 1);
  transition: all 0.5s ease-in-out;
}

.TodoSearch__button:hover {
  background-color: cornflowerblue;
  transition: all 0.5s ease-in-out;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}

.p--search {
  color: whitesmoke;
}
ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }
.TodoItem {
  width: calc(100% - 50px);
  margin: 24px auto;
  position: relative;

  background: #3BBCFD;
  box-shadow: 0px 5px 50px rgba(13, 87, 235, 0.15);
  border-radius: 5px 13px 5px;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}

.TodoItem:hover {
  box-shadow: 5px 7px 7px rgb(134, 231, 177);
}

.TodoItem-p {
  margin: 20px;
  width: calc(100% - 120px);

  color: #403E06;
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;

}

.TodoItem-p--complete {
  text-decoration: line-through;
  color: #73015A;
  font-weight: bold;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}

.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  right: 0;
}

.Icon-delete:hover {
  color: red;
}

.TodoItem--input {
  cursor: pointer;
  background-color: transparent;
  background-image: url(/TODO-appIntro-react/static/media/check2.6e0e9826.png);
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  left: 2px;
  width: 50px;
  height: 50px;
  border-radius: 5px 13px 5px;
  -webkit-appearance: none;
          appearance: none;
}

.TodoItem--input:hover {
  background-color: #49dc1c;
  transition: background-color 0.5s ease-in;
}

.TodoItem--input:checked {
  background-image: url(/TODO-appIntro-react/static/media/checked2.8792590e.png);
  background-color: #73015A;
  transition: background-color 0.5s ease-in-out;
}
form {
  width: 90%;
  max-width: 300px;
  /* background-color: #fff; */
  background: rgb(48, 73, 213);
  background: radial-gradient(circle, rgba(16, 47, 225, 0.996) 5%,
   rgb(23, 31, 76) 100%);
  border-radius: 30px;
  padding: 20px 30px;
  display: flex;
  align-content: center;
  flex-direction: column;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 26px;
  color: #ACFF8D;
}

textarea {
  background-color: #F9FBFC;
  border: 2px, solid #202329;
  border-radius: 20px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}

textarea::-webkit-input-placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
}

textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Montserrat';
  font-weight: 400;
}

textarea:focus {
  outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  margin-left: 10px;
  margin-top: 10px;
  font-size: 20px;
  color: #b3072c;
  font-weight: 600;
  width: 120px;
  height: 48px;
  border-radius: 30px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button:hover {
   color: #78031A;
   background-color: #FFD176;
   transition: background 0.3s ease-in;
}

.TodoForm-button-add {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button-cancel {
  background: transparent;
}
.CreateTodoButton {
  background: rgb(242, 133, 255);
  background: linear-gradient(265deg, rgba(242, 133, 255, 0.7525385154061625) 29%, rgba(65, 209, 255, 1) 95%);
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  z-index: 1;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: .3s ease;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}
.ModalBackground {
  background: rgba(32, 35, 41, .8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.container--error{
    width: calc(100%-30px);
    height: calc(100vh + 30px);

    display: flex;
    flex-direction: column;
    align-items: center;
     
}

.error--text{
    width: 100%;
    margin-bottom: 20px;

    text-align: center;
    font-size: 2.5rem;
}

#error--img{
    width: 250px;

    background: transparent;
}

.container--loading{
    width: 100%;
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.container--loading__img{
    width: 200px;

    background: transparent;
    margin-bottom: 30px;
}

.container--loading__text{
    width: calc(100%-30px);
    height: auto;

    margin: 0 auto;
    text-align: center;
    font-size: 2.5rem;
}

    
.container-empty{
    width: calc(100%-30px);
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;
    font-size: 2.5rem;
}

#container-empty__img {
    width: 200px;
}
